import { createServerClient, type CookieOptions } from '@supabase/ssr'
import { NextResponse, type NextRequest } from 'next/server'

export async function middleware(request: NextRequest) {
  let response = NextResponse.next({
    request: {
      headers: request.headers,
    },
  })

  const supabaseUrl = process.env.NEXT_PUBLIC_SUPABASE_URL
  const supabaseAnonKey = process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY

  // Si no hay configuracion de Supabase, dejar pasar
  if (!supabaseUrl || !supabaseAnonKey) {
    return response
  }

  const supabase = createServerClient(
    supabaseUrl,
    supabaseAnonKey,
    {
      cookies: {
        get(name: string) {
          return request.cookies.get(name)?.value
        },
        set(name: string, value: string, options: CookieOptions) {
          request.cookies.set({ name, value, ...options })
          response = NextResponse.next({
            request: { headers: request.headers },
          })
          response.cookies.set({ name, value, ...options })
        },
        remove(name: string, options: CookieOptions) {
          request.cookies.set({ name, value: '', ...options })
          response = NextResponse.next({
            request: { headers: request.headers },
          })
          response.cookies.set({ name, value: '', ...options })
        },
      },
    }
  )

  const { data: { session } } = await supabase.auth.getSession()
  const pathname = request.nextUrl.pathname

  // Rutas de autenticacion (login, registro)
  const authRoutes = ['/login', '/registro']
  const isAuthRoute = authRoutes.some(route => pathname === route)

  // Rutas de onboarding
  const isOnboardingRoute = pathname.startsWith('/onboarding')

  // Rutas protegidas que requieren perfil completo
  const protectedRoutes = ['/unirse', '/equipos', '/equipo']
  const isProtectedRoute = protectedRoutes.some(route => pathname.startsWith(route))

  // Si no hay sesion
  if (!session) {
    // Permitir rutas de auth
    if (isAuthRoute) {
      return response
    }
    // Redirigir a login si intenta acceder a ruta protegida u onboarding
    if (isProtectedRoute || isOnboardingRoute) {
      return NextResponse.redirect(new URL('/login', request.url))
    }
    return response
  }

  // Si hay sesion, verificar estado del perfil
  const { data: perfil } = await supabase
    .from('perfiles')
    .select('nombre, genero, edad, peso_kg, altura_cm, perfil_completo')
    .eq('id', session.user.id)
    .single()

  // Determinar si el perfil esta completo
  const perfilCompleto = perfil && (
    perfil.perfil_completo === true ||
    (perfil.nombre && perfil.genero && perfil.edad && perfil.peso_kg && perfil.altura_cm)
  )

  // Si hay sesion y esta en ruta de auth -> redirigir segun perfil
  if (isAuthRoute) {
    if (perfilCompleto) {
      return NextResponse.redirect(new URL('/equipos', request.url))
    } else {
      return NextResponse.redirect(new URL('/onboarding/nombre', request.url))
    }
  }

  // Si el perfil NO esta completo
  if (!perfilCompleto) {
    // Permitir onboarding
    if (isOnboardingRoute) {
      return response
    }
    // Bloquear rutas protegidas -> redirigir a onboarding
    if (isProtectedRoute) {
      return NextResponse.redirect(new URL('/onboarding/nombre', request.url))
    }
  }

  // Si el perfil esta completo y esta en onboarding -> redirigir a equipos
  if (perfilCompleto && isOnboardingRoute) {
    return NextResponse.redirect(new URL('/equipos', request.url))
  }

  return response
}

export const config = {
  matcher: [
    '/login',
    '/registro',
    '/unirse',
    '/equipos',
    '/equipos/:path*',
    '/equipo/:path*',
    '/onboarding/:path*',
  ],
}
